/* 导入布局模块 */

/* 保留的特定布局样式 */
/* 这里放置一些不适合分类到具体模块的特殊布局样式 */

/* 特定组件布局样式 */
/* 这里放置一些不适合分类到具体模块的特殊布局样式 */

/* 表单区域特定样式 */
.form-section {
  background: var(--gray-100);
  padding: var(--spacing-xl);
  border-radius: var(--border-radius-md);
  box-shadow: var(--box-shadow-md);
  width: 600px;
  margin: 0 auto;
}

/* 星盘区域特定样式 */
.astrolabe-section {
  background: var(--gray-100);
  padding: var(--spacing-lg);
  border-radius: var(--border-radius-md);
  box-shadow: var(--box-shadow-md);
}

.astrolabe-header {
  text-align: center;
  margin-bottom: var(--spacing-lg);
}

.astrolabe-header h2 {
  color: var(--gray-800);
  margin-bottom: var(--spacing-sm);
}

.astrolabe-header p {
  color: var(--gray-600);
  margin: 0;
}

.astrolabe-container {
  overflow: auto;
  height: 100%;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* AI聊天区域特定样式 */
.ai-chat-section {
  margin-top: var(--spacing-xl);
}

/* 结果区域特定样式 */
.result-section {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  background: var(--white);
  padding: var(--spacing-lg);
  border-radius: var(--border-radius-md);
  box-shadow: var(--box-shadow-lg);
  height: calc(100vh - 80px);
}

/* 标签导航特定样式 */
.tab-navigation {
  display: flex;
  justify-content: center;
  margin-bottom: var(--spacing-lg);
}

.tab-content {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  min-height: 0;
  overflow: hidden;
}

/* 星盘内容特定样式 */
.astrolabe-content {
  height: 100%;
  overflow: hidden;
}

/* AI内容特定样式 */
.ai-content {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  min-height: 0;
}

/* 合盘模式特定样式 */
.synastry-container {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: flex-start;
  width: 100%;
  gap: var(--spacing-lg);
  flex-wrap: wrap;
}

.synastry-person {
  flex: 1;
  min-width: 300px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.synastry-person h3 {
  margin-bottom: var(--spacing-md);
  color: var(--gray-800);
  text-align: center;
}

.rechart-button-container {
  display: flex;
  justify-content: center;
  margin-top: var(--spacing-lg);
  width: 100%;
}

/* 隐藏垂直滚动条但保持滚动功能 */
.astrolabe-content::-webkit-scrollbar {
  width: 0px;
  background: transparent;
}

.synastry-container::-webkit-scrollbar {
  width: 0px;
  background: transparent;
}

/* Firefox隐藏滚动条 */
.astrolabe-content {
  scrollbar-width: none;
  -ms-overflow-style: none;
}

.synastry-container {
  scrollbar-width: none;
  -ms-overflow-style: none;
}

/* 加载指示器特定样式 */
.loading-indicator {
  padding: var(--spacing-xl);
  font-size: var(--font-size-md);
  color: var(--gray-600);
  background-color: var(--gray-100);
  border-radius: var(--border-radius-md);
  margin: var(--spacing-lg) 0;
}

.loading-indicator::after {
  content: '';
  width: 20px;
  height: 20px;
  margin-left: var(--spacing-sm);
  border: 2px solid var(--gray-300);
  border-top-color: var(--primary-color);
  border-radius: var(--border-radius-full);
  animation: spin 1s linear infinite;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

/* 后台AI生成指示器特定样式 */
.background-ai-indicator {
  position: absolute;
  top: 10px;
  right: 10px;
  background-color: rgba(0, 123, 255, 0.9);
  color: white;
  padding: 6px 12px;
  border-radius: 20px;
  font-size: 12px;
  display: flex;
  align-items: center;
  gap: 6px;
  box-shadow: 0 2px 8px rgba(0, 123, 255, 0.3);
  z-index: 10;
  animation: pulse 2s infinite;
}

.indicator-content {
  display: flex;
  align-items: center;
  gap: 6px;
}

.indicator-text {
  font-size: 12px;
  white-space: nowrap;
}

/* 后台AI指示器中的打字动画 */
.background-ai-indicator .typing-indicator {
  display: flex;
  align-items: center;
}

.background-ai-indicator .typing-indicator span {
  height: 6px;
  width: 6px;
  background-color: white;
  border-radius: var(--border-radius-full);
  display: inline-block;
  margin: 0 1px;
  animation: typing 1.4s infinite;
}

.background-ai-indicator .typing-indicator span:nth-child(2) {
  animation-delay: 0.2s;
}

.background-ai-indicator .typing-indicator span:nth-child(3) {
  animation-delay: 0.4s;
}

@keyframes typing {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-3px);
  }
}

@keyframes pulse {
  0% {
    box-shadow: 0 2px 8px rgba(0, 123, 255, 0.3);
  }
  50% {
    box-shadow: 0 2px 12px rgba(0, 123, 255, 0.5);
  }
  100% {
    box-shadow: 0 2px 8px rgba(0, 123, 255, 0.3);
  }
}

.rechart-button-container {
  margin-top: var(--spacing-lg);
  width: 100%;
