 * Flexbox布局模块
 * 专门处理Flexbox布局相关的样式
 */

/* 基础Flexbox容器 */
.flex-container {
  display: flex;
}

.flex-column {
  flex-direction: column;
}

.flex-row {
  flex-direction: row;
}

.flex-wrap {
  flex-wrap: wrap;
}

.flex-nowrap {
  flex-wrap: nowrap;
}

/* 对齐方式 */
.flex-start {
  justify-content: flex-start;
}

.flex-end {
  justify-content: flex-end;
}

.flex-center {
  justify-content: center;
}

.flex-between {
  justify-content: space-between;
}

.flex-around {
  justify-content: space-around;
}

.flex-evenly {
  justify-content: space-evenly;
}

/* 交叉轴对齐 */
.items-start {
  align-items: flex-start;
}

.items-end {
  align-items: flex-end;
}

.items-center {
  align-items: center;
}

.items-baseline {
  align-items: baseline;
}

.items-stretch {
  align-items: stretch;
}

/* 多行对齐 */
.content-start {
  align-content: flex-start;
}

.content-end {
  align-content: flex-end;
}

.content-center {
  align-content: center;
}

.content-between {
  align-content: space-between;
}

.content-around {
  align-content: space-around;
}

.content-stretch {
  align-content: stretch;
}

/* 项目属性 */
.flex-1 {
  flex: 1;
}

.flex-auto {
  flex: auto;
}

.flex-none {
  flex: none;
}

.flex-grow {
  flex-grow: 1;
}

.flex-shrink {
  flex-shrink: 1;
}

.flex-shrink-0 {
  flex-shrink: 0;
}

.self-start {
  align-self: flex-start;
}

.self-end {
  align-self: flex-end;
}

.self-center {
  align-self: center;
}

.self-baseline {
  align-self: baseline;
}

.self-stretch {
  align-self: stretch;
}

/* 应用特定Flexbox布局 */
.app-flex {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.form-flex {
  display: flex;
  flex-direction: column;
  max-width: 600px;
  margin: 0 auto;
  gap: var(--spacing-md);
}

.result-flex {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  gap: var(--spacing-lg);
}

.tab-navigation-flex {
  display: flex;
  justify-content: center;
  margin-bottom: var(--spacing-lg);
}

.tab-content-flex {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  min-height: 0;
}

.synastry-flex {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: flex-start;
  width: 100%;
  gap: var(--spacing-lg);
  flex-wrap: wrap;
}

.synastry-person-flex {
  flex: 1;
  min-width: 300px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.astrolabe-flex {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  width: 100%;
}

.rechart-button-flex {
  display: flex;
  justify-content: center;
  margin-top: var(--spacing-lg);
  width: 100%;
}

.loading-indicator-flex {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: var(--spacing-xl);
  font-size: var(--font-size-md);
  color: var(--gray-600);
  background-color: var(--gray-100);
  border-radius: var(--border-radius-md);
  margin: var(--spacing-lg) 0;
}

.background-ai-indicator-flex {
  display: flex;
  align-items: center;
  gap: var(--spacing-xs);
}

/* 响应式Flexbox布局调整 */
@media (max-width: 768px) {
  .app-flex {
    height: 100vh;
    height: 100dvh;
  }
  
  .form-flex {
    width: 100%;
    padding: var(--spacing-md);
    border-radius: 0;
    box-shadow: none;
    background-color: var(--white);
    height: 100vh;
    justify-content: flex-start;
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
    box-sizing: border-box;
    position: relative;
  }
  
  .result-flex {
    border-radius: 0;
    box-shadow: none;
    padding: 0;
    height: 100vh;
    margin: 0;
    width: 100%;
    overflow: hidden;
    box-sizing: border-box;
    background-color: var(--white);
  }
  
  .tab-navigation-flex {
    padding: var(--spacing-sm);
    background-color: var(--white);
    border-bottom: 1px solid var(--gray-300);
    width: 100%;
    box-sizing: border-box;
    margin: 0;
    position: sticky;
    top: env(safe-area-inset-top);
    z-index: 10;
  }
  
  .tab-content-flex {
    min-height: 300px;
    overflow: hidden;
  }
  
  .synastry-flex {
    flex-direction: column;
    align-items: center;
    width: 100%;
    height: 100%;
    overflow: auto;
    -webkit-overflow-scrolling: touch;
    padding: var(--spacing-sm);
    box-sizing: border-box;
  }
  
  .synastry-person-flex {
    width: 100%;
    margin-bottom: var(--spacing-md);
    background-color: var(--white);
    border-radius: var(--border-radius-md);
    padding: var(--spacing-md);
    box-shadow: var(--box-shadow-sm);
    box-sizing: border-box;
  }
  
  .astrolabe-flex {
    padding: 0 var(--spacing-sm);
    box-sizing: border-box;
  }
  
  .rechart-button-flex {
    margin-top: var(--spacing-md);
    margin-bottom: var(--spacing-md);
    width: 100%;
    padding: 0 var(--spacing-md);
    box-sizing: border-box;
    padding-bottom: max(var(--spacing-md), env(safe-area-inset-bottom));
  }
}

/* 超小屏幕Flexbox布局调整 */
@media (max-width: 480px) {
  .form-flex {
    padding: var(--spacing-sm);
  }
  
  .astrolabe-flex {
    padding: 0 var(--spacing-xs);
  }
  
  .rechart-button-flex {
    padding: 0 var(--spacing-sm);
  }
