___CSS_LOADER_EXPORT___.push([module.id, `/* 历史记录模态窗样式 */

.history-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  backdrop-filter: blur(4px);
  padding: var(--spacing-md);
}

.history-modal {
  background-color: var(--background-color);
  border-radius: var(--border-radius-lg);
  box-shadow: 0 12px 24px rgba(0, 0, 0, 0.15);
  width: 90%;
  max-width: 500px;
  max-height: 85vh;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  animation: modalFadeIn 0.3s ease-out;
  border: 1px solid var(--border-color);
}

@keyframes modalFadeIn {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.history-modal-header {
  padding: var(--spacing-lg) var(--spacing-lg) var(--spacing-md);
  background-color: var(--background-color);
  position: relative;
}

.history-title {
  margin: 0;
  font-size: var(--font-size-xl);
  color: var(--text-color);
  cursor: pointer;
  user-select: none;
  font-weight: 600;
  text-align: center;
}

.history-password-container {
  display: flex;
  padding: var(--spacing-md) var(--spacing-lg);
  gap: var(--spacing-md);
  background-color: var(--background-color);
  flex-direction: column;
  align-items: stretch;
}

.history-password-input {
  flex: 1;
  padding: var(--spacing-md);
  border: 1px solid var(--input-border);
  border-radius: var(--border-radius-md);
  background-color: var(--input-bg);
  color: var(--text-color);
  font-size: var(--font-size-md);
  box-sizing: border-box;
  transition: border-color var(--transition-normal) ease, box-shadow var(--transition-normal) ease;
}

.history-password-input:focus {
  outline: none;
  border-color: var(--iztro-color-active);
  box-shadow: 0 0 0 2px rgba(123, 85, 239, 0.2);
}

.history-password-input::placeholder {
  color: var(--text-secondary);
  opacity: 0.7;
}

.history-password-button {
  padding: var(--spacing-md) var(--spacing-lg);
  background-color: var(--iztro-color-active);
  color: white;
  border: none;
  border-radius: var(--border-radius-md);
  cursor: pointer;
  font-size: var(--font-size-md);
  font-weight: 500;
  transition: background-color var(--transition-normal) ease, transform var(--transition-fast) ease;
  white-space: nowrap;
  min-width: 80px;
  box-shadow: var(--box-shadow-sm);
}

.history-password-button:hover:not(:disabled) {
  background-color: var(--primary-hover);
  transform: translateY(-1px);
  box-shadow: var(--box-shadow-md);
}

.history-password-button:active:not(:disabled) {
  transform: translateY(0);
}

.history-password-button:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  background-color: var(--gray-400);
  box-shadow: none;
}

.history-search-container {
  padding: var(--spacing-md) var(--spacing-lg);
  background-color: var(--background-color);
  width: 100%;
  box-sizing: border-box;
}

.history-search-input {
  width: 100%;
  padding: var(--spacing-md);
  border: 1px solid var(--input-border);
  border-radius: var(--border-radius-md);
  background-color: var(--input-bg);
  color: var(--text-color);
  font-size: var(--font-size-md);
  transition: border-color var(--transition-normal) ease, box-shadow var(--transition-normal) ease;
  box-sizing: border-box;
}

.history-search-input:focus {
  outline: none;
  border-color: var(--iztro-color-active);
  box-shadow: 0 0 0 2px rgba(123, 85, 239, 0.2);
}

.history-search-input::placeholder {
  color: var(--text-secondary);
  opacity: 0.7;
}

.history-modal-content {
  flex: 1;
  overflow-y: auto;
  padding: var(--spacing-lg);
  background-color: var(--background-color);
}

.history-empty {
  text-align: center;
  color: var(--text-secondary);
  padding: var(--spacing-2xl);
  font-style: italic;
  font-size: var(--font-size-md);
}

.history-list {
  display: flex;
  flex-direction: column;
  gap: var(--spacing-md);
}

.history-item {
  border: 1px solid var(--border-color);
  border-radius: var(--border-radius-md);
  overflow: hidden;
  transition: all var(--transition-normal) ease;
  background-color: var(--background-color);
  box-shadow: var(--box-shadow-sm);
}

.history-item:hover {
  box-shadow: var(--box-shadow-md);
  border-color: var(--iztro-color-active);
  transform: translateY(-1px);
}

.history-item-content {
  padding: var(--spacing-sm) var(--spacing-md);
  cursor: pointer;
  transition: background-color var(--transition-normal) ease;
  display: flex;
  align-items: center;
  justify-content: space-between;
  min-height: 48px;
}

.history-item-content:hover {
  background-color: var(--gray-100);
}

.history-item-main {
  flex: 1;
  min-width: 0;
  display: flex;
  align-items: center;
  gap: var(--spacing-md);
}

.history-item-name {
  font-weight: 700;
  color: var(--text-color);
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  font-size: var(--font-size-xl);
  line-height: 1.3;
  flex-shrink: 0;
}

.history-item-info {
  color: var(--text-secondary);
  font-size: var(--font-size-sm);
  font-weight: 400;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  opacity: 0.7;
}

.history-item-actions {
  display: flex;
  gap: var(--spacing-xs);
  margin-left: var(--spacing-md);
  flex-shrink: 0;
  align-items: center;
}

.history-item-content .history-item-actions .history-edit-icon,
.history-item-content .history-item-actions .history-delete-icon {
  background: none !important;
  border: none !important;
  cursor: pointer;
  font-size: 16px !important;
  padding: var(--spacing-sm) !important;
  border-radius: var(--border-radius-md) !important;
  transition: all var(--transition-normal) ease;
  display: flex !important;
  align-items: center !important;
  justify-content: center !important;
  width: 32px !important;
  height: 32px !important;
  color: var(--text-secondary) !important;
  margin: 0 !important;
  margin-top: 0 !important;
  box-shadow: none !important;
}

.history-edit-icon:hover,
.history-delete-icon:hover {
  background-color: var(--gray-200);
  transform: scale(1.05);
}

.history-delete-icon:hover {
  color: var(--danger-color);
}

.history-modal-footer {
  padding: var(--spacing-lg) var(--spacing-lg) var(--spacing-xl);
  background-color: var(--background-color);
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.history-clear-button {
  padding: var(--spacing-md) var(--spacing-xl);
  background-color: var(--iztro-color-awesome);
  color: white;
  border: none;
  border-radius: var(--border-radius-md);
  cursor: pointer;
  font-size: var(--font-size-md);
  transition: all var(--transition-normal) ease;
  margin: 0 auto;
  display: block;
  font-weight: 500;
  box-shadow: var(--box-shadow-sm);
}

.history-clear-button:hover:not(:disabled) {
  background-color: #e8590c;
  transform: translateY(-1px);
  box-shadow: var(--box-shadow-md);
}

.history-clear-button:active:not(:disabled) {
  transform: translateY(0);
}

.history-clear-button:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  background-color: var(--gray-400);
  box-shadow: none;
}

/* 暗色模式样式 */
@media (prefers-color-scheme: dark) {
  .history-modal {
    background-color: var(--background-color);
    border-color: var(--border-color);
  }
  
  .history-modal-header,
  .history-modal-footer {
    background-color: var(--background-color);
    border-color: var(--border-color);
  }
  
  .history-password-input,
  .history-search-input {
    background-color: var(--input-bg);
    border-color: var(--input-border);
    color: var(--text-color);
  }
  
  .history-item-content:hover {
    background-color: var(--gray-700);
  }
  
  .history-edit-icon:hover,
  .history-delete-icon:hover {
    background-color: var(--gray-700);
  }
  
  .history-item {
    background-color: var(--background-color);
    border-color: var(--border-color);
    box-shadow: none;
  }
  
  .history-item-name {
    color: var(--text-color);
  }
  
  .history-item-info {
    color: var(--text-secondary);
  }
}

/* 移动端适配 */
@media (max-width: 768px) {
  .history-modal-overlay {
    padding: var(--spacing-sm);
  }
  
  .history-modal {
    width: 95%;
    max-height: 90vh;
    margin: 0;
    border-radius: var(--border-radius-lg);
  }
  
  .history-modal-header {
    padding: var(--spacing-md) var(--spacing-md) var(--spacing-sm);
  }
  
  .history-search-container {
    padding: var(--spacing-sm) var(--spacing-md);
  }
  
  .history-password-container {
    padding: var(--spacing-sm) var(--spacing-md);
    gap: var(--spacing-sm);
    flex-direction: column;
    align-items: stretch;
  }
  
  .history-modal-content {
    padding: var(--spacing-md);
  }
  
  .history-modal-footer {
    padding: var(--spacing-md) var(--spacing-md) var(--spacing-lg);
  }
  
  .history-password-input {
    padding: var(--spacing-md);
    font-size: var(--font-size-md);
    border-radius: var(--border-radius-md);
  }
  
  .history-password-button {
    padding: var(--spacing-md);
    font-size: var(--font-size-md);
    border-radius: var(--border-radius-md);
    width: 100%;
  }
  
  .history-password-button:hover:not(:disabled) {
    background-color: var(--primary-hover);
  }
  
  .history-item-content {
    padding: var(--spacing-sm) var(--spacing-md);
    min-height: 52px;
  }
  
  .history-item-main {
    gap: var(--spacing-sm);
  }
  
  .history-item-name {
    font-size: var(--font-size-xl);
    font-weight: 700;
  }
  
  .history-item-info {
    font-size: var(--font-size-sm);
    opacity: 0.7;
  }
  
  .history-item-actions {
    gap: var(--spacing-xs);
    align-items: center;
  }
  
  .history-item-content .history-item-actions .history-edit-icon,
  .history-item-content .history-item-actions .history-delete-icon {
    width: 36px !important;
    height: 36px !important;
    font-size: 18px !important;
    padding: var(--spacing-sm) !important;
    box-shadow: none !important;
  }
  
  .history-title {
    font-size: var(--font-size-lg);
  }
  
  .history-search-input {
    padding: var(--spacing-md);
  }
  
  .history-clear-button {
    padding: var(--spacing-md) var(--spacing-xl);
    width: 100%;
  }
}
