___CSS_LOADER_EXPORT___.push([module.id, `.iztro-astrolabe {
  font-family: -apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Helvetica Neue,Arial,Noto Sans,sans-serif,Apple Color Emoji,Segoe UI Emoji,Segoe UI Symbol,Noto Color Emoji;
  display: grid;
  position: relative;
  width: 100%;
  grid-gap: 3px;
  grid-template-columns: repeat(4, 1fr);
  grid-auto-rows: 1fr;
  grid-template-areas:
    "g3 g4 g5 g6"
    "g2 ct ct g7"
    "g1 ct ct g8"
    "g0 g11 g10 g9";
}

/* 移动端响应式布局 */
@media (max-width: 768px) {
  .iztro-astrolabe {
    grid-template-columns: repeat(4, 1fr);
    grid-template-areas:
      "g3 g4 g5 g6"
      "g2 ct ct g7"
      "g1 ct ct g8"
      "g0 g11 g10 g9";
    grid-auto-rows: minmax(60px, auto);
    grid-gap: 2px;
  }
}

/* 超小屏幕设备 */
@media (max-width: 480px) {
  .iztro-astrolabe {
    grid-template-columns: repeat(4, 1fr);
    grid-template-areas:
      "g3 g4 g5 g6"
      "g2 ct ct g7"
      "g1 ct ct g8"
      "g0 g11 g10 g9";
    grid-auto-rows: minmax(50px, auto);
    grid-gap: 1px;
  }
}

.iztro-star-mutagen {
  font-weight: normal;
  font-size: var(--iztro-star-font-size-small);
  border-radius: 4px;
  color: #fff;
  display: inline-block;
  margin-left: 1px;
  padding: 0 2px;
}

.star-with-mutagen {
  position: relative;
}

.star-with-mutagen::before {
  bottom: 0;
  content: " ";
  left: -4px;
  position: absolute;
  top: 0;
  width: 4px;
  transition: all 0.25s ease-in-out;
}

.star-with-mutagen::after {
  content: " ";
  position: absolute;
  left: 0;
  bottom: -4px;
  right: 0;
  height: 4px;
  transition: all 0.25s ease-in-out;
