/* 消息操作按钮样式 */
.message-actions-container {
  display: flex;
  justify-content: flex-start;
  width: 100%;
  z-index: 1;
  gap: 8px;
  margin-top: 8px;
  /* 性能优化：减少重绘和回流 */
  will-change: auto;
  contain: layout;
}

.copy-button,
.share-button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 32px;
  height: 32px;
  padding: 0;
  background-color: transparent;
  border: none;
  border-radius: 50%;
  color: var(--text-secondary);
  cursor: pointer;
  transition: all var(--transition-normal) ease;
  flex-shrink: 0;
  /* 性能优化：减少重绘和回流 */
  will-change: transform, background-color;
  /* 优化按钮点击性能 */
  transform: translateZ(0);
  backface-visibility: hidden;
}

.copy-button:hover,
.share-button:hover {
  background-color: var(--quick-action-bg);
  color: var(--primary-color);
  transform: translateY(-1px);
}

.copy-button:active,
.share-button:active {
  transform: translateY(0);
}

/* 按钮图标样式 */
.copy-button svg,
.share-button svg {
  width: 16px;
  height: 16px;
  stroke-width: 2;
}

/* 去除移动端点击后的默认蓝色高光 */
.copy-button,
.share-button {
  -webkit-tap-highlight-color: transparent;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  user-select: none;
