/*
 * 响应式布局模块
 * 专门处理响应式设计相关的样式
 */

/* 全局响应式样式 */
body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-color: var(--gray-100);
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* App容器响应式样式 */
.App {
  max-width: 1200px;
  margin: 0 auto;
  padding: var(--spacing-lg);
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

/* 内容区域响应式样式 */
.content {
  display: flex;
  flex-direction: column;
  height: 100%;
}

/* 响应式特定样式 - 基础布局已在layout.css中定义 */

/* 加载指示器响应式样式 */
.loading-indicator {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: var(--spacing-xl);
  font-size: var(--font-size-md);
  color: var(--gray-600);
  background-color: var(--gray-100);
  border-radius: var(--border-radius-md);
  margin: var(--spacing-lg) 0;
}

.loading-indicator::after {
  content: '';
  width: 20px;
  height: 20px;
  margin-left: var(--spacing-sm);
  border: 2px solid var(--gray-300);
  border-top-color: var(--primary-color);
  border-radius: var(--border-radius-full);
  animation: spin 1s linear infinite;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

/* 后台AI生成指示器响应式样式 */
.background-ai-indicator {
  position: absolute;
  top: 10px;
  right: 10px;
  background-color: rgba(0, 123, 255, 0.9);
  color: white;
  padding: 6px 12px;
  border-radius: 20px;
  font-size: 12px;
  display: flex;
  align-items: center;
  gap: 6px;
  box-shadow: 0 2px 8px rgba(0, 123, 255, 0.3);
  z-index: 10;
  animation: pulse 2s infinite;
}

.indicator-content {
  display: flex;
  align-items: center;
  gap: 6px;
}

.indicator-text {
  font-size: 12px;
  white-space: nowrap;
}

/* 后台AI指示器中的打字动画 */
.background-ai-indicator .typing-indicator {
  display: flex;
  align-items: center;
}

.background-ai-indicator .typing-indicator span {
  height: 6px;
  width: 6px;
  background-color: white;
  border-radius: var(--border-radius-full);
  display: inline-block;
  margin: 0 1px;
  animation: typing 1.4s infinite;
}

.background-ai-indicator .typing-indicator span:nth-child(2) {
  animation-delay: 0.2s;
}

.background-ai-indicator .typing-indicator span:nth-child(3) {
  animation-delay: 0.4s;
}

@keyframes typing {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-3px);
  }
}

@keyframes pulse {
  0% {
    box-shadow: 0 2px 8px rgba(0, 123, 255, 0.3);
  }
  50% {
    box-shadow: 0 2px 12px rgba(0, 123, 255, 0.5);
  }
  100% {
    box-shadow: 0 2px 8px rgba(0, 123, 255, 0.3);
  }
}

/* 移动端全局样式 - 全屏沉浸式体验 */
@media (max-width: 768px) {
  body {
    background-color: var(--white);
    overflow-x: hidden;
    position: fixed;
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
    /* 移除任何可能的边框或阴影 */
    border: none;
    box-shadow: none;
    /* 确保全屏显示 */
    min-height: 100vh;
    min-height: 100dvh;
    /* 防止滚动条 */
    overflow: hidden;
  }
  
  .App {
    padding: 0;
    max-width: 100%;
    width: 100%;
    height: 100vh;
    height: 100dvh;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    position: relative;
    padding-top: env(safe-area-inset-top);
    padding-bottom: env(safe-area-inset-bottom);
  }
  
  .content {
    flex-direction: column;
    height: 100%;
    overflow: hidden;
  }
}

/* 移动端表单区域样式 - 全屏沉浸式体验 */
@media (max-width: 768px) {
  .form-section {
    width: 100%;
    padding: var(--spacing-md);
    border-radius: 0;
    box-shadow: none;
    background-color: var(--white);
    height: 100vh;
    height: 100dvh;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
    box-sizing: border-box;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    /* 全屏沉浸式体验 */
    margin: 0;
    max-width: 100%;
    /* 安全区域适配 */
    padding-top: max(var(--spacing-md), env(safe-area-inset-top));
    padding-bottom: max(var(--spacing-md), env(safe-area-inset-bottom));
    /* iOS Safari 特殊处理：确保内容区域正确计算 */
    min-height: -webkit-fill-available;
    /* 防止内容被工具栏遮挡 */
    padding-bottom: calc(var(--spacing-xl) + env(safe-area-inset-bottom) + 80px);
  }
  
  /* 合盘表单区域优化 */
  .form-section form {
    max-width: 100%;
    overflow: visible;
    box-sizing: border-box;
    /* 确保表单底部有足够空间显示按钮 */
    padding-bottom: calc(var(--spacing-xl) + env(safe-area-inset-bottom) + 40px);
  }
  
  /* 合盘表单分组优化 */
  .form-section h3 {
    font-size: var(--font-size-lg);
    margin: var(--spacing-lg) 0 var(--spacing-md);
    padding-bottom: var(--spacing-sm);
    border-bottom: 1px solid var(--gray-300);
    color: var(--gray-800);
    font-weight: 600;
  }
  
  /* 第一个分组不添加上边距 */
  .form-section h3:first-of-type {
    margin-top: var(--spacing-sm);
  }
}

/* 移动端星盘区域样式 - 全屏沉浸式体验 */
@media (max-width: 768px) {
  .astrolabe-section {
    background: var(--white);
    padding: 0;
    border-radius: 0;
    box-shadow: none;
    margin: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    position: relative;
    /* 全屏沉浸式体验 */
    min-height: 100vh;
    min-height: 100dvh;
    /* 适配安全区域 */
    padding-top: env(safe-area-inset-top);
    padding-bottom: env(safe-area-inset-bottom);
  }
  
  .astrolabe-container {
    width: 100%;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0;
    box-sizing: border-box;
    /* 全屏沉浸式体验 */
    margin: 0;
    border: none;
    border-radius: 0;
    box-shadow: none;
    background: transparent;
    overflow: hidden;
    /* 适配安全区域 */
    padding-top: env(safe-area-inset-top);
    padding-bottom: env(safe-area-inset-bottom);
  }
  
  .iztro-astrolabe {
    min-width: 280px;
    max-width: 100%;
    margin: 0 auto;
    transform-origin: center;
  }
  
  .rechart-button-container {
    margin-top: var(--spacing-md);
    margin-bottom: var(--spacing-md);
    width: 100%;
    padding: 0 var(--spacing-md);
    box-sizing: border-box;
    padding-bottom: max(var(--spacing-md), env(safe-area-inset-bottom));
  }
  
  .rechart-button {
    width: 100%;
    padding: var(--spacing-sm);
    font-size: var(--font-size-md);
  }
}

/* 超小屏幕设备 */
@media (max-width: 480px) {
  .astrolabe-container {
    padding: 0 var(--spacing-xs);
  }
  
  .iztro-astrolabe {
    min-width: 260px;
    max-width: 100%;
  }
  
  .rechart-button-container {
    padding: 0 var(--spacing-sm);
  }
}

/* 移动端result-section样式 - 全屏沉浸式体验 */
@media (max-width: 768px) {
  .result-section {
    border-radius: 0;
    box-shadow: none;
    padding: 0;
    height: 100vh;
    height: 100dvh;
    margin: 0;
    width: 100%;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
    background-color: var(--white);
    /* 全屏沉浸式体验 */
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    /* 确保内容填满整个屏幕 */
    max-width: 100%;
    max-height: 100%;
    /* 确保完全填满屏幕，不显示卡片部分 */
    min-height: 100vh;
    min-height: 100dvh;
    /* 移除任何可能的边距 */
    border: none;
    outline: none;
  }
  
  .tab-navigation {
    padding: var(--spacing-sm);
    background-color: var(--white);
    border-bottom: 1px solid var(--gray-300);
    width: 100%;
    box-sizing: border-box;
    margin: 0;
    position: sticky;
    top: env(safe-area-inset-top);
    z-index: 10;
  }
  
  .tab-content {
    flex: 1;
    overflow: hidden;
    height: calc(100vh - 44px);
    height: calc(100dvh - 44px);
    /* 确保完全填满屏幕，不显示卡片部分 */
    min-height: calc(100vh - 44px);
    min-height: calc(100dvh - 44px);
    max-height: calc(100vh - 44px);
    max-height: calc(100dvh - 44px);
    padding: 0;
    margin: 0;
    width: 100%;
    box-sizing: border-box;
    /* 全屏沉浸式体验 */
    min-height: 0;
    position: relative;
  }
  
  .ai-content {
    flex-grow: 1;
    overflow: hidden;
    padding: 0;
    margin: 0;
    width: 100%;
    box-sizing: border-box;
    /* 全屏沉浸式体验 */
    min-height: 0;
    position: relative;
    /* 适配安全区域 */
    padding-top: env(safe-area-inset-top);
    padding-bottom: env(safe-area-inset-bottom);
  }
}

/* 移动端星盘内容样式 - 全屏沉浸式体验 */
@media (max-width: 768px) {
  .astrolabe-content {
    height: 100%;
    overflow: auto;
    -webkit-overflow-scrolling: touch;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    padding: 0;
    margin: 0;
    width: 100%;
    /* 全屏沉浸式体验 */
    min-height: 100vh;
    min-height: 100dvh;
    position: relative;
    /* 移除任何卡片式外观 */
    border: none;
    border-radius: 0;
    box-shadow: none;
    background: transparent;
    /* 适配安全区域 */
    padding-top: env(safe-area-inset-top);
    padding-bottom: env(safe-area-inset-bottom);
  }
}

/* 在小屏幕上垂直排列 */
@media (max-width: 768px) {
  .synastry-container {
    flex-direction: column;
    align-items: center;
    width: 100%;
    height: 100%;
    overflow: auto;
    -webkit-overflow-scrolling: touch;
    padding: var(--spacing-sm);
    box-sizing: border-box;
    /* 全屏沉浸式体验 */
    min-height: 0;
    position: relative;
    /* 适配安全区域 */
    padding-top: max(var(--spacing-sm), env(safe-area-inset-top));
    padding-bottom: max(var(--spacing-sm), env(safe-area-inset-bottom));
  }
  
  .synastry-person {
    width: 100%;
    margin-bottom: var(--spacing-md);
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: var(--white);
    border-radius: var(--border-radius-md);
    padding: var(--spacing-md);
    box-shadow: var(--box-shadow-sm);
    box-sizing: border-box;
  }
  
  .synastry-person:last-child {
    margin-bottom: 0;
  }
  
  .synastry-person h3 {
    margin-bottom: var(--spacing-sm);
    font-size: var(--font-size-md);
    color: var(--gray-800);
    font-weight: 600;
    text-align: center;
    width: 100%;
    padding-bottom: var(--spacing-sm);
    border-bottom: 1px solid var(--gray-200);
  }
}

/* 隐藏垂直滚动条但保持滚动功能 */
.astrolabe-content::-webkit-scrollbar {
  width: 0px;
  background: transparent;
}

.synastry-container::-webkit-scrollbar {
  width: 0px;
  background: transparent;
}

/* Firefox隐藏滚动条 */
.astrolabe-content {
  scrollbar-width: none;
  -ms-overflow-style: none;
}

.synastry-container {
  scrollbar-width: none;
  -ms-overflow-style: none;
}

/* 移动端后台AI指示器样式 */
@media (max-width: 768px) {
  .background-ai-indicator {
    top: 5px;
    right: 5px;
    padding: 4px 8px;
  }
  
  .indicator-text {
    font-size: 10px;
  }
  
  .background-ai-indicator .typing-indicator span {
    height: 4px;
    width: 4px;
  }
}
