Cf,iBAAiB;IACjB,sBAAsB;IACtB,oBAAoB;IACpB,4EAA4E;EAC9E;;EAEA,aAAa;EACb;IACE,8BAA8B;IAC9B,6CAA6C;IAC7C,iCAAiC;IACjC,wCAAwC;IACxC,sBAAsB;IACtB,gBAAgB;EAClB;;EAEA,gBAAgB;EAChB;IACE,6BAA6B;EAC/B;AACF;;AAEA,wBAAwB;AACxB;EACE;IACE,wBAAwB;IACxB,UAAU;IACV,gBAAgB;IAChB,gBAAgB;IAChB,SAAS;IACT,WAAW;IACX,YAAY;IACZ,gBAAgB;IAChB,kBAAkB;IAClB,YAAY;IACZ,iBAAiB;IACjB,kBAAkB;IAClB,WAAW;IACX,qCAAqC;IACrC,2CAA2C;EAC7C;;EAEA;IACE,WAAW;IACX,eAAe;IACf,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,UAAU;IACV,sBAAsB;IACtB,YAAY;IACZ,SAAS;IACT,YAAY;IACZ,gBAAgB;IAChB,gBAAgB;IAChB,uBAAuB;IACvB,gBAAgB;IAChB,WAAW;IACX,qCAAqC;IACrC,2CAA2C;EAC7C;;EAEA;IACE,gBAAgB;IAChB,eAAe;IACf,cAAc;IACd,wBAAwB;EAC1B;;EAEA;IACE,6BAA6B;IAC7B,gCAAgC;IAChC,WAAW;IACX,4BAA4B;IAC5B,sBAAsB;IACtB,mEAAmE;EACrE;;EAEA;IACE,WAAW;IACX,0BAA0B;IAC1B,8BAA8B;EAChC;AACF;;AAEA,WAAW;AACX;EACE;IACE,4BAA4B;EAC9B;;EAEA;IACE,gBAAgB;IAChB,eAAe;EACjB;;EAEA;IACE,4BAA4B;EAC9B;AACF;;AAEA,kCAAkC;AAClC;EACE;IACE,gBAAgB;IAChB,gBAAgB;IAChB,UAAU;IACV,aAAa;IACb,cAAc;IACd,SAAS;IACT,WAAW;IACX,gBAAgB;IAChB,aAAa;IACb,sBAAsB;IACtB,sBAAsB;IACtB,8BAA8B;IAC9B,YAAY;IACZ,eAAe;IACf,MAAM;IACN,OAAO;IACP,QAAQ;IACR,SAAS;IACT,eAAe;IACf,eAAe;IACf,gBAAgB;IAChB,qBAAqB;IACrB,iBAAiB;IACjB,kBAAkB;IAClB,cAAc;IACd,YAAY;IACZ,aAAa;EACf;;EAEA;IACE,0BAA0B;IAC1B,8BAA8B;IAC9B,wCAAwC;IACxC,WAAW;IACX,sBAAsB;IACtB,SAAS;IACT,gBAAgB;IAChB,6BAA6B;IAC7B,WAAW;EACb;;EAEA;IACE,OAAO;IACP,gBAAgB;IAChB,0BAA0B;IAC1B,2BAA2B;IAC3B,qBAAqB;IACrB,8BAA8B;IAC9B,+BAA+B;IAC/B,8BAA8B;IAC9B,+BAA+B;IAC/B,UAAU;IACV,SAAS;IACT,WAAW;IACX,sBAAsB;IACtB,YAAY;IACZ,aAAa;IACb,kBAAkB;EACpB;;EAEA;IACE,YAAY;IACZ,gBAAgB;IAChB,UAAU;IACV,SAAS;IACT,WAAW;IACX,sBAAsB;IACtB,YAAY;IACZ,aAAa;IACb,kBAAkB;IAClB,WAAW;IACX,qCAAqC;IACrC,2CAA2C;EAC7C;AACF;;AAEA,wBAAwB;AACxB;EACE;IACE,YAAY;IACZ,cAAc;IACd,iCAAiC;IACjC,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,2BAA2B;IAC3B,UAAU;IACV,SAAS;IACT,WAAW;IACX,YAAY;IACZ,iBAAiB;IACjB,kBAAkB;IAClB,kBAAkB;IAClB,cAAc;IACd,YAAY;IACZ,gBAAgB;IAChB,gBAAgB;IAChB,uBAAuB;IACvB,WAAW;IACX,qCAAqC;IACrC,2CAA2C;EAC7C;AACF;;AAEA,cAAc;AACd;EACE;IACE,sBAAsB;IACtB,mBAAmB;IACnB,WAAW;IACX,YAAY;IACZ,cAAc;IACd,iCAAiC;IACjC,0BAA0B;IAC1B,sBAAsB;IACtB,YAAY;IACZ,aAAa;IACb,kBAAkB;IAClB,WAAW;IACX,6DAA6D;IAC7D,mEAAmE;EACrE;;EAEA;IACE,WAAW;IACX,gCAAgC;IAChC,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,8BAA8B;IAC9B,sCAAsC;IACtC,0BAA0B;IAC1B,gCAAgC;IAChC,sBAAsB;EACxB;;EAEA;IACE,gBAAgB;EAClB;;EAEA;IACE,gCAAgC;IAChC,8BAA8B;IAC9B,sBAAsB;IACtB,gBAAgB;IAChB,kBAAkB;IAClB,WAAW;IACX,iCAAiC;IACjC,wCAAwC;EAC1C;AACF;;AAEA,mBAAmB;AACnB;EACE,UAAU;EACV,uBAAuB;AACzB;;AAEA;EACE,UAAU;EACV,uBAAuB;AACzB;;AAEA,iBAAiB;AACjB;EACE,qBAAqB;EACrB,wBAAwB;AAC1B;;AAEA;EACE,qBAAqB;EACrB,wBAAwB;AAC1B;;AAEA,iBAAiB;AACjB;EACE;IACE,QAAQ;IACR,UAAU;IACV,gBAAgB;EAClB;;EAEA;IACE,eAAe;EACjB;;EAEA;IACE,WAAW;IACX,UAAU;EACZ;AACF","sourcesContent":["/* \n * 响应式布局模块\n * 专门处理响应式设计相关的样式\n */\n\n/*\n * 响应式布局模块\n * 专门处理响应式设计相关的样式\n */\n\n/* 全局响应式样式 */\nbody {\n  margin: 0;\n  padding: 0;\n  box-sizing: border-box;\n  background-color: var(--gray-100);\n  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;\n  -webkit-font-smoothing: antialiased;\n  -moz-osx-font-smoothing: grayscale;\n}\n\n/* App容器响应式样式 */\n.App {\n  max-width: 1200px;\n  margin: 0 auto;\n  padding: var(--spacing-lg);\n  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;\n}\n\n/* 内容区域响应式样式 */\n.content {\n  display: flex;\n  flex-direction: column;\n  height: 100%;\n}\n\n/* 响应式特定样式 - 基础布局已在layout.css中定义 */\n\n/* 加载指示器响应式样式 */\n.loading-indicator {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  padding: var(--spacing-xl);\n  font-size: var(--font-size-md);\n  color: var(--gray-600);\n  background-color: var(--gray-100);\n  border-radius: var(--border-radius-md);\n  margin: var(--spacing-lg) 0;\n}\n\n.loading-indicator::after {\n  content: '';\n  width: 20px;\n  height: 20px;\n  margin-left: var(--spacing-sm);\n  border: 2px solid var(--gray-300);\n  border-top-color: var(--primary-color);\n  border-radius: var(--border-radius-full);\n  animation: spin 1s linear infinite;\n}\n\n@keyframes spin {\n  to {\n    transform: rotate(360deg);\n  }\n}\n\n/* 后台AI生成指示器响应式样式 */\n.background-ai-indicator {\n  position: absolute;\n  top: 10px;\n  right: 10px;\n  background-color: rgba(0, 123, 255, 0.9);\n  color: white;\n  padding: 6px 12px;\n  border-radius: 20px;\n  font-size: 12px;\n  display: flex;\n  align-items: center;\n  gap: 6px;\n  box-shadow: 0 2px 8px rgba(0, 123, 255, 0.3);\n  z-index: 10;\n  animation: pulse 2s infinite;\n}\n\n.indicator-content {\n  display: flex;\n  align-items: center;\n  gap: 6px;\n}\n\n.indicator-text {\n  font-size: 12px;\n  white-space: nowrap;\n}\n\n/* 后台AI指示器中的打字动画 */\n.background-ai-indicator .typing-indicator {\n  display: flex;\n  align-items: center;\n}\n\n.background-ai-indicator .typing-indicator span {\n  height: 6px;\n  width: 6px;\n  background-color: white;\n  border-radius: var(--border-radius-full);\n  display: inline-block;\n  margin: 0 1px;\n  animation: typing 1.4s infinite;\n}\n\n.background-ai-indicator .typing-indicator span:nth-child(2) {\n  animation-delay: 0.2s;\n}\n\n.background-ai-indicator .typing-indicator span:nth-child(3) {\n  animation-delay: 0.4s;\n}\n\n@keyframes typing {\n  0%, 100% {\n    transform: translateY(0);\n  }\n  50% {\n    transform: translateY(-3px);\n  }\n}\n\n@keyframes pulse {\n  0% {\n    box-shadow: 0 2px 8px rgba(0, 123, 255, 0.3);\n  }\n  50% {\n    box-shadow: 0 2px 12px rgba(0, 123, 255, 0.5);\n  }\n  100% {\n    box-shadow: 0 2px 8px rgba(0, 123, 255, 0.3);\n  }\n}\n\n/* 移动端全局样式 - 全屏沉浸式体验 */\n@media (max-width: 768px) {\n  body {\n    background-color: var(--white);\n    overflow-x: hidden;\n    position: fixed;\n    width: 100%;\n    height: 100%;\n    margin: 0;\n    padding: 0;\n    /* 移除任何可能的边框或阴影 */\n    border: none;\n    box-shadow: none;\n    /* 确保全屏显示 */\n    min-height: 100vh;\n    min-height: 100dvh;\n    /* 防止滚动条 */\n    overflow: hidden;\n  }\n  \n  .App {\n    padding: 0;\n    max-width: 100%;\n    width: 100%;\n    height: 100vh;\n    height: 100dvh;\n    display: flex;\n    flex-direction: column;\n    overflow: hidden;\n    position: relative;\n    padding-top: env(safe-area-inset-top);\n    padding-bottom: env(safe-area-inset-bottom);\n  }\n  \n  .content {\n    flex-direction: column;\n    height: 100%;\n    overflow: hidden;\n  }\n}\n\n/* 移动端表单区域样式 - 全屏沉浸式体验 */\n@media (max-width: 768px) {\n  .form-section {\n    width: 100%;\n    padding: var(--spacing-md);\n    border-radius: 0;\n    box-shadow: none;\n    background-color: var(--white);\n    height: 100vh;\n    height: 100dvh;\n    display: flex;\n    flex-direction: column;\n    justify-content: flex-start;\n    overflow-y: auto;\n    -webkit-overflow-scrolling: touch;\n    box-sizing: border-box;\n    position: fixed;\n    top: 0;\n    left: 0;\n    right: 0;\n    bottom: 0;\n    /* 全屏沉浸式体验 */\n    margin: 0;\n    max-width: 100%;\n    /* 安全区域适配 */\n    padding-top: max(var(--spacing-md), env(safe-area-inset-top));\n    padding-bottom: max(var(--spacing-md), env(safe-area-inset-bottom));\n    /* iOS Safari 特殊处理：确保内容区域正确计算 */\n    min-height: -webkit-fill-available;\n    /* 防止内容被工具栏遮挡 */\n    padding-bottom: calc(var(--spacing-xl) + env(safe-area-inset-bottom) + 80px);\n  }\n  \n  /* 合盘表单区域优化 */\n  .form-section form {\n    max-width: 100%;\n    overflow: visible;\n    box-sizing: border-box;\n    /* 确保表单底部有足够空间显示按钮 */\n    padding-bottom: calc(var(--spacing-xl) + env(safe-area-inset-bottom) + 40px);\n  }\n  \n  /* 合盘表单分组优化 */\n  .form-section h3 {\n    font-size: var(--font-size-lg);\n    margin: var(--spacing-lg) 0 var(--spacing-md);\n    padding-bottom: var(--spacing-sm);\n    border-bottom: 1px solid var(--gray-300);\n    color: var(--gray-800);\n    font-weight: 600;\n  }\n  \n  /* 第一个分组不添加上边距 */\n  .form-section h3:first-of-type {\n    margin-top: var(--spacing-sm);\n  }\n}\n\n/* 移动端星盘区域样式 - 全屏沉浸式体验 */\n@media (max-width: 768px) {\n  .astrolabe-section {\n    background: var(--white);\n    padding: 0;\n    border-radius: 0;\n    box-shadow: none;\n    margin: 0;\n    width: 100%;\n    height: 100%;\n    overflow: hidden;\n    position: relative;\n    /* 全屏沉浸式体验 */\n    min-height: 100vh;\n    min-height: 100dvh;\n    /* 适配安全区域 */\n    padding-top: env(safe-area-inset-top);\n    padding-bottom: env(safe-area-inset-bottom);\n  }\n  \n  .astrolabe-container {\n    width: 100%;\n    max-width: 100%;\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    padding: 0;\n    box-sizing: border-box;\n    /* 全屏沉浸式体验 */\n    margin: 0;\n    border: none;\n    border-radius: 0;\n    box-shadow: none;\n    background: transparent;\n    overflow: hidden;\n    /* 适配安全区域 */\n    padding-top: env(safe-area-inset-top);\n    padding-bottom: env(safe-area-inset-bottom);\n  }\n  \n  .iztro-astrolabe {\n    min-width: 280px;\n    max-width: 100%;\n    margin: 0 auto;\n    transform-origin: center;\n  }\n  \n  .rechart-button-container {\n    margin-top: var(--spacing-md);\n    margin-bottom: var(--spacing-md);\n    width: 100%;\n    padding: 0 var(--spacing-md);\n    box-sizing: border-box;\n    padding-bottom: max(var(--spacing-md), env(safe-area-inset-bottom));\n  }\n  \n  .rechart-button {\n    width: 100%;\n    padding: var(--spacing-sm);\n    font-size: var(--font-size-md);\n  }\n}\n\n/* 超小屏幕设备 */\n@media (max-width: 480px) {\n  .astrolabe-container {\n    padding: 0 var(--spacing-xs);\n  }\n  \n  .iztro-astrolabe {\n    min-width: 260px;\n    max-width: 100%;\n  }\n  \n  .rechart-button-container {\n    padding: 0 var(--spacing-sm);\n  }\n}\n\n/* 移动端result-section样式 - 全屏沉浸式体验 */\n@media (max-width: 768px) {\n  .result-section {\n    border-radius: 0;\n    box-shadow: none;\n    padding: 0;\n    height: 100vh;\n    height: 100dvh;\n    margin: 0;\n    width: 100%;\n    overflow: hidden;\n    display: flex;\n    flex-direction: column;\n    box-sizing: border-box;\n    background-color: var(--white);\n    /* 全屏沉浸式体验 */\n    position: fixed;\n    top: 0;\n    left: 0;\n    right: 0;\n    bottom: 0;\n    /* 确保内容填满整个屏幕 */\n    max-width: 100%;\n    max-height: 100%;\n    /* 确保完全填满屏幕，不显示卡片部分 */\n    min-height: 100vh;\n    min-height: 100dvh;\n    /* 移除任何可能的边距 */\n    border: none;\n    outline: none;\n  }\n  \n  .tab-navigation {\n    padding: var(--spacing-sm);\n    background-color: var(--white);\n    border-bottom: 1px solid var(--gray-300);\n    width: 100%;\n    box-sizing: border-box;\n    margin: 0;\n    position: sticky;\n    top: env(safe-area-inset-top);\n    z-index: 10;\n  }\n  \n  .tab-content {\n    flex: 1;\n    overflow: hidden;\n    height: calc(100vh - 44px);\n    height: calc(100dvh - 44px);\n    /* 确保完全填满屏幕，不显示卡片部分 */\n    min-height: calc(100vh - 44px);\n    min-height: calc(100dvh - 44px);\n    max-height: calc(100vh - 44px);\n    max-height: calc(100dvh - 44px);\n    padding: 0;\n    margin: 0;\n    width: 100%;\n    box-sizing: border-box;\n    /* 全屏沉浸式体验 */\n    min-height: 0;\n    position: relative;\n  }\n  \n  .ai-content {\n    flex-grow: 1;\n    overflow: hidden;\n    padding: 0;\n    margin: 0;\n    width: 100%;\n    box-sizing: border-box;\n    /* 全屏沉浸式体验 */\n    min-height: 0;\n    position: relative;\n    /* 适配安全区域 */\n    padding-top: env(safe-area-inset-top);\n    padding-bottom: env(safe-area-inset-bottom);\n  }\n}\n\n/* 移动端星盘内容样式 - 全屏沉浸式体验 */\n@media (max-width: 768px) {\n  .astrolabe-content {\n    height: 100%;\n    overflow: auto;\n    -webkit-overflow-scrolling: touch;\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    justify-content: flex-start;\n    padding: 0;\n    margin: 0;\n    width: 100%;\n    /* 全屏沉浸式体验 */\n    min-height: 100vh;\n    min-height: 100dvh;\n    position: relative;\n    /* 移除任何卡片式外观 */\n    border: none;\n    border-radius: 0;\n    box-shadow: none;\n    background: transparent;\n    /* 适配安全区域 */\n    padding-top: env(safe-area-inset-top);\n    padding-bottom: env(