___CSS_LOADER_EXPORT___.push([module.id, `/* 
 * 网格布局模块
 * 专门处理CSS Grid布局相关的样式
 */

/* 主容器网格布局 */
.app-grid {
  display: grid;
  grid-template-rows: auto 1fr auto;
  min-height: 100vh;
  gap: var(--spacing-md);
}

/* 表单区域网格布局 */
.form-grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: var(--spacing-md);
  max-width: 600px;
  margin: 0 auto;
}

/* 结果区域网格布局 */
.result-grid {
  display: grid;
  grid-template-rows: auto 1fr;
  gap: var(--spacing-lg);
  height: 100%;
}

/* 标签导航网格布局 */
.tab-navigation-grid {
  display: grid;
  grid-auto-flow: column;
  grid-auto-columns: 1fr;
  gap: var(--spacing-sm);
}

/* 合盘模式网格布局 */
.synastry-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: var(--spacing-lg);
  width: 100%;
}

/* 星盘内容网格布局 */
.astrolabe-grid {
  display: grid;
  grid-template-rows: auto 1fr auto;
  gap: var(--spacing-md);
  height: 100%;
  align-items: center;
  justify-items: center;
}

/* 响应式网格布局调整 */
@media (max-width: 768px) {
  .app-grid {
    grid-template-rows: auto 1fr auto;
    gap: 0;
    height: 100vh;
    height: 100dvh;
  }
  
  .form-grid {
    grid-template-columns: 1fr;
    max-width: 100%;
    padding: var(--spacing-md);
  }
  
  .result-grid {
    grid-template-rows: auto 1fr;
    gap: 0;
    height: 100vh;
    height: 100dvh;
  }
  
  .synastry-grid {
    grid-template-columns: 1fr;
    gap: var(--spacing-md);
  }
  
  .astrolabe-grid {
    grid-template-rows: auto 1fr auto;
    gap: var(--spacing-sm);
    padding: var(--spacing-sm);
  }
}

/* 超小屏幕网格布局调整 */
@media (max-width: 480px) {
  .form-grid {
    padding: var(--spacing-sm);
  }
  
  .astrolabe-grid {
    padding: var(--spacing-xs);
  }
}`, "",{"version":3,"sources":["webpack://./src/styles/layout/grid.css"],"names":[],"mappings":"AAAA;;;EAGE;;AAEF,YAAY;AACZ;EACE,aAAa;EACb,iCAAiC;EACjC,iBAAiB;EACjB,sBAAsB;AACxB;;AAEA,aAAa;AACb;EACE,aAAa;EACb,0BAA0B;EAC1B,sBAAsB;EACtB,gBAAgB;EAChB,cAAc;AAChB;;AAEA,aAAa;AACb;EACE,aAAa;EACb,4BAA4B;EAC5B,sBAAsB;EACtB,YAAY;AACd;;AAEA,aAAa;AACb;EACE,aAAa;EACb,sBAAsB;EACtB,sBAAsB;EACtB,sBAAsB;AACxB;;AAEA,aAAa;AACb;EACE,aAAa;EACb,2DAA2D;EAC3D,sBAAsB;EACtB,WAAW;AACb;;AAEA,aAAa;AACb;EACE,aAAa;EACb,iCAAiC;EACjC,sBAAsB;EACtB,YAAY;EACZ,mBAAmB;EACnB,qBAAqB;AACvB;;AAEA,cAAc;AACd;EACE;IACE,iCAAiC;IACjC,MAAM;IACN,aAAa;IACb,cAAc;EAChB;;EAEA;IACE,0BAA0B;IAC1B,eAAe;IACf,0BAA0B;EAC5B;;EAEA;IACE,4BAA4B;IAC5B,MAAM;IACN,aAAa;IACb,cAAc;EAChB;;EAEA;IACE,0BAA0B;IAC1B,sBAAsB;EACxB;;EAEA;IACE,iCAAiC;IACjC,sBAAsB;IACtB,0BAA0B;EAC5B;AACF;;AAEA,eAAe;AACf;EACE;IACE,0BAA0B;EAC5B;;EAEA;IACE,0BAA0B;EAC5B;AACF","sourcesContent":["/* \n * 网格布局模块\n * 专门处理CSS Grid布局相关的样式\n */\n\n/* 主容器网格布局 */\n.app-grid {\n  display: grid;\n  grid-template-rows: auto 1fr auto;\n  min-height: 100vh;\n  gap: var(--spacing-md);\n}\n\n/* 表单区域网格布局 */\n.form-grid {\n  display: grid;\n  grid-template-columns: 1fr;\n  gap: var(--spacing-md);\n  max-width: 600px;\n  margin: 0 auto;\n}\n\n/* 结果区域网格布局 */\n.result-grid {\n  display: grid;\n  grid-template-rows: auto 1fr;\n  gap: var(--spacing-lg);\n  height: 100%;\n}\n\n/* 标签导航网格布局 */\n.tab-navigation-grid {\n  display: grid;\n  grid-auto-flow: column;\n  grid-auto-columns: 1fr;\n  gap: var(--spacing-sm);\n}\n\n/* 合盘模式网格布局 */\n.synastry-grid {\n  display: grid;\n  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));\n  gap: var(--spacing-lg);\n  width: 100%;\n}\n\n/* 星盘内容网格布局 */\n.astrolabe-grid {\n  display: grid;\n  grid-template-rows: auto 1fr auto;\n  gap: var(--spacing-md);\n  height: 100%;\n  align-items: center;\n  justify-items: center;\n}\n\n/* 响应式网格布局调整 */\n@media (max-width: 768px) {\n  .app-grid {\n    grid-template-rows: auto 1fr auto;\n    gap: 0;\n    height: 