@media (prefers-color-scheme: dark) {
  .history-button {
    background-color: transparent;
    color: var(--iztro-color-active);
  }
  
  .history-button:hover {
    background-color: transparent;
    filter: brightness(1.2);
  }
}

/* 滚动到底部按钮样式 */
.scroll-to-bottom-button {
  position: fixed;
  bottom: 100px;
  left: 50%;
