  z-index: 1000;
  opacity: 0;
  pointer-events: none;
}

.scroll-to-bottom-button.visible {
  opacity: 1;
  transform: scale(1);
  pointer-events: auto;
}

.scroll-to-bottom-button:hover {
  background-color: var(--iztro-color-active);
  transform: scale(1.1);
