btn-danger {\n  background-color: var(--iztro-color-awesome);\n}\n\n.btn-warning {\n  background-color: var(--iztro-color-decorator-1);\n  color: var(--gray-800);\n}\n\n.btn-info {\n  background-color: var(--iztro-color-quan);\n}\n\n.btn-light {\n  background-color: var(--gray-200);\n  color: var(--gray-800);\n}\n\n.btn-dark {\n  background-color: var(--gray-800);\n  color: var(--white);\n}\n\n.btn-link {\n  background-color: transparent;\n  color: var(--iztro-color-active);\n  text-decoration: none;\n  box-shadow: none;\n}\n\n/* 按钮悬停效果 */\n.btn-primary:hover,\n.btn-secondary:hover,\n.btn-success:hover,\n.btn-danger:hover,\n.btn-warning:hover,\n.btn-info:hover,\n.btn-link:hover {\n  filter: brightness(1.1);\n}\n\n.btn-light:hover {\n  background-color: var(--gray-300);\n}\n\n.btn-dark:hover {\n  background-color: var(--gray-900);\n}\n\n.btn-link:hover {\n  text-decoration: underline;\n}\n\n/* 按钮大小 */\n.btn-sm {\n  padding: var(--spacing-sm) var(--spacing-md);\n  font-size: var(--font-size-sm);\n  border-radius: var(--border-radius-sm);\n}\n\n.btn-md {\n  padding: var(--spacing-md) var(--spacing-lg);\n  font-size: var(--font-size-md);\n  border-radius: var(--border-radius-md);\n}\n\n.btn-lg {\n  padding: var(--spacing-lg) var(--spacing-xl);\n  font-size: var(--font-size-lg);\n  border-radius: var(--border-radius-md);\n}\n\n.btn-block {\n  display: block;\n  width: 100%;\n}\n\n/* 按钮状态 */\n.btn:disabled,\n.btn.disabled {\n  opacity: 0.65;\n  cursor: not-allowed;\n  pointer-events: none;\n}\n\n.btn:focus,\n.btn.focus {\n  outline: 0;\n  box-shadow: 0 0 0 2px rgba(123, 85, 239, 0.2);\n}\n\n/* 轮廓按钮 - 统一过渡效果 */\n.btn-outline-primary,\n.btn-outline-secondary,\n.btn-outline-success,\n.btn-outline-danger,\n.btn-outline-warning,\n.btn-outline-info,\n.btn-outline-light,\n.btn-outline-dark {\n  background-color: transparent;\n  border: 1px solid;\n  transition: background-color var(--transition-normal) ease, color var(--transition-normal) ease;\n}\n\n.btn-outline-primary {\n  color: var(--primary-color);\n  border-color: var(--primary-color);\n}\n\n.btn-outline-secondary {\n  color: var(--iztro-color-text);\n  border-color: var(--iztro-color-text);\n}\n\n.btn-outline-success {\n  color: var(--iztro-color-nice);\n  border-color: var(--iztro-color-nice);\n}\n\n.btn-outline-danger {\n  color: var(--iztro-color-awesome);\n  border-color: var(--iztro-color-awesome);\n}\n\n.btn-outline-warning {\n  color: var(--iztro-color-decorator-1);\n  border-color: var(--iztro-color-decorator-1);\n}\n\n.btn-outline-info {\n  color: var(--iztro-color-quan);\n  border-color: var(--iztro-color-quan);\n}\n\n.btn-outline-light {\n  color: var(--gray-200);\n  border-color: var(--gray-200);\n}\n\n.btn-outline-dark {\n  color: var(--gray-800);\n  border-color: var(--gray-800);\n}\n\n/* 轮廓按钮悬停效果 */\n.btn-outline-primary:hover,\n.btn-outline-secondary:hover,\n.btn-outline-success:hover,\n.btn-outline-danger:hover,\n.btn-outline-info:hover {\n  color: var(--white);\n}\n\n.btn-outline-primary:hover {\n  background-color: var(--primary-color);\n}\n\n.btn-outline-secondary:hover {\n  background-color: var(--iztro-color-text);\n}\n\n.btn-outline-success:hover {\n  background-color: var(--iztro-color-nice);\n}\n\n.btn-outline-danger:hover {\n  background-color: var(--iztro-color-awesome);\n}\n\n.btn-outline-warning:hover {\n  background-color: var(--iztro-color-decorator-1);\n  color: var(--gray-800);\n}\n\n.btn-outline-info:hover {\n  background-color: var(--iztro-color-quan);\n}\n\n.btn-outline-light:hover {\n  background-color: var(--gray-200);\n  color: var(--gray-800);\n}\n\n.btn-outline-dark:hover {\n  background-color: var(--gray-800);\n  color: var(--white);\n}\n\n/* 移动端轮廓按钮优化 */\n@media (max-width: 768px) {\n  .btn-outline-primary,\n  .btn-outline-secondary,\n  .btn-outline-success,\n  .btn-outline-danger,\n  .btn-outline-warning,\n  .btn-outline-info,\n  .btn-outline-light,\n  .btn-outline-dark {\n    min-height: 36px;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    -webkit-tap-highlight-color: transparent;\n    user-select: none;\n  }\n}\n\n/* 暗色模式按钮样式 */\n@media (prefers-color-scheme: dark) {\n  .btn {\n    background-color: var(--primary-color);\n    color: white;\n  }\n  \n  .btn:hover {\n    filter: brightness(1.2);\n  }\n  \n  .rechart-button {\n    background-color: var(--primary-color);\n    color: white;\n  }\n  \n  .btn-warning {\n    color: var(--gray-800);\n  }\n  \n  .btn-outline-warning:hover {\n    color: var(--gray-800);\n  }\n  \n  .btn-light {\n    background-color: var(--gray-700);\n    color: var(--white);\n  }\n  \n  .btn-light:hover {\n    background-color: var(--gray-600);\n  }\n  \n  .btn-outline-light {\n    color: var(--gray-400);\n    border-color: var(--gray-400);\n  }\n  \n  .btn-outline-light:hover {\n    background-color: var(--gray-400);\n    color: var(--gray-800);\n  }\n}\n\n/* 历史记录按钮 - 纯文字样式 */\n.history-button {\n  background-color: transparent;\n  color: var(--iztro-color-active);\n  border: none;\n  padding: 0;\n  margin: var(--spacing-md) auto 0;\n  font-size: var(--font-size-md);\n  cursor: pointer;\n  text-decoration: none;\n  box-shadow: none;\n  font-weight: normal;\n  display: block;\n  width: auto;\n  transition: color var(--transition-normal) ease;\n  text-align: center;\n}\n\n.history-button:hover {\n  background-color: transparent;\n  color: var(--iztro-color-active);\n  filter: brightness(1.2);\n  text-decoration: underline;\n}\n\n/* 移动端历史记录按钮样式 */\n@media (max-width: 768px) {\n  .history-button {\n    padding: 0;\n    margin: var(--spacing-md) auto 0;\n    font-size: var(--font-size-md);\n    min-height: auto;\n    touch-action: manipulation;\n    -webkit-tap-highlight-color: transparent;\n    user-select: none;\n    width: auto;\n    display: block;\n    text-align: center;\n    /* iOS Safari 特殊处理：确保历史记录按钮在底部完全可见 */\n    margin-bottom: calc(var(--spacing-md) + env(safe-area-inset-bottom) + 20px);\n    /* 确保按钮不会被键盘或工具栏遮挡 */\n    position: relative;\n    z-index: 10;\n  }\n  \n  .history-button:active {\n    transform: none;\n    box-shadow: none;\n  }\n}\n\n/* 暗色模式历史记录按钮样式 */\n@media (prefers-color-scheme: dark) {\n  .history-button {\n    background-color: transparent;\n    color: var(--iztro-color-active);\n  }\n  \n  .history-button:hover {\n    background-color: transparent;\n    filter: brightness(1.2);\n  }\n}\n\n/* 滚动到底部按钮样式 */\n.scroll-to-bottom-button {\n  position: fixed;\n  bottom: 100px;\n  left: 50%;\n  transform: translateX(-50%) scale(0.8);\n  width: 48px;\n  height: 48px;\n  border-radius: 50%;\n  background-color: var(--primary-color);\n  color: white;\n  border: none;\n  cursor: pointer;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  box-shadow: var(--box-shadow-md);\n  transition: all var(--transition-normal) ease;\n  z-index: 1000;\n  opacity: 0;\n  pointer-events: none;\n}\n\n.scroll-to-bottom-button.visible {\n  opacity: 1;\n  transform: scale(1);\n  pointer-events: auto;\n}\n\n.scroll-to-bottom-button:hover {\n  background-color: var(--iztro-color-active);\n  transform: scale(1.1);\n  box-shadow: var(--box-shadow-lg);\n}\n\n.scroll-to-bottom-button:active {\n  transform: scale(0.95);\n}\n\n/* 移动端滚动到底部按钮样式 */\n@media (max-width: 768px) {\n  .scroll-to-bottom-button {\n    bottom: 120px;\n    left: 50%;\n    transform: translateX(-50%) scale(0.8);\n    width: 44px;\n    height: 44px;\n    touch-action: manipulation;\n    -webkit-tap-highlight-color: transparent;\n    user-select: none;\n  }\n  \n  .scroll-to-bottom-button.visible {\n    transform: translateX(-50%) scale(1);\n  }\n  \n  .scroll-to-bottom-button:hover {\n    transform: translateX(-50%) scale(1.05);\n  }\n  \n  .scroll-to-bottom-button:active {\n    transform: translateX(-50%) scale(0.9);\n  }\n  \n  /* 移动端 SVG 图标放大 */\n  .scroll-to-bottom-button svg {\n    width: 28px;\n    height: 28px;\n  }\n}\n\n/* 暗色模式滚动到底部按钮样式 */\n@media (prefers-color-scheme: dark) {\n  .scroll-to-bottom-button {\n    background-color: var(--primary-color);\n    color: white;\n  }\n  \n  .scroll-to-bottom-button:hover {\n    background-color: var(--iztro-color-active);\n    filter: brightness(1.2);\n  }\n}\n"],"sourceRoot":""}]);
