.question-panel {
  padding: var(--spacing-lg);
  max-height: 350px;
  overflow-y: auto;
  background-color: transparent;
  margin-bottom: 0;
}

.question-tabs {
  display: flex;
  margin-bottom: var(--spacing-sm);
  overflow-x: auto;
  -ms-overflow-style: none;
  scrollbar-width: none;
  /* 减少高度 */
  min-height: 32px;
  /* 移除阴影 */
  box-shadow: none;
  /* 减少内边距 */
  padding: 0;
}

.question-tabs::-webkit-scrollbar {
  display: none;
}

.tab-button {
  padding: var(--spacing-sm) var(--spacing-md);
  cursor: pointer;
  background-color: transparent;
  border: none;
  border-bottom: 2px solid transparent;
  color: var(--gray-600);
  font-size: var(--font-size-md);
  white-space: nowrap;
  transition: color var(--transition-normal), border-color var(--transition-normal);
  /* 禁用移动端点击时的蓝色高亮 */
  -webkit-tap-highlight-color: transparent;
  -webkit-user-select: none;
  user-select: none;
  /* 确保不继承button的margin-top */
  margin-top: 0;
}

.tab-button.active {
  color: var(--primary-color);
  font-weight: 600;
}

.tab-button:hover {
  color: var(--primary-color);
  background-color: transparent;
}

.question-content {
  height: calc(100% - 48px);
  overflow-y: auto;
  padding-right: var(--spacing-sm);
}

.question-group {
  margin-bottom: var(--spacing-md);
}

.category-heading {
  font-size: var(--font-size-sm);
  font-weight: 600;
  color: var(--gray-800);
  margin-bottom: var(--spacing-sm);
  margin-top: 0;
}

.questions-wrapper {
  display: flex;
  flex-wrap: wrap;
  gap: var(--spacing-sm);
  /* 确保选项之间有足够的间距 */
  row-gap: var(--spacing-sm);
}

.question-text {
  background-color: var(--gray-100);
  border-radius: 12px;
  padding: var(--spacing-sm) var(--spacing-md);
  font-size: var(--font-size-sm);
  cursor: pointer;
  color: var(--gray-800);
  line-height: 1.4;
  margin: 0;
  transition: background-color var(--transition-normal);
  /* 禁用移动端点击时的蓝色高亮 */
  -webkit-tap-highlight-color: transparent;
  -webkit-user-select: none;
  user-select: none;
}

.question-text:hover {
  background-color: var(--gray-200);
}

@media (max-width: 768px) {
  .question-panel {
    padding: var(--spacing-md);
    margin-bottom: var(--spacing-md);
    max-height: 200px;
    overflow-y: auto;
    background-color: transparent;
    /* 保持与电脑端一致的简洁样式 */
  }
  
  .question-tabs {
    margin-bottom: var(--spacing-md);
    overflow-x: auto;
    -ms-overflow-style: none;
    scrollbar-width: none;
    /* 与电脑端保持一致 */
  }
  
  .tab-button {
    padding: var(--spacing-sm) var(--spacing-md);
    font-size: var(--font-size-sm);
    /* 与电脑端保持一致 */
    /* 确保不继承button的margin-top */
    margin-top: 0;
    /* 去除阴影 */
    box-shadow: none;
  }
  
  .question-text {
    background-color: var(--gray-100);
    border-radius: 12px;
    padding: var(--spacing-sm) var(--spacing-md);
    font-size: var(--font-size-sm);
    color: var(--gray-800);
    line-height: 1.4;
    margin: 0;
    /* 与电脑端保持一致 */
  }
  
  .question-text:hover {
    background-color: var(--gray-200);
  }
  
  /* 键盘可见时的问题面板样式 */
  .keyboard-visible .question-panel {
    max-height: 200px;
  }
}

/* 隐藏垂直滚动条但保持滚动功能 */
.question-content::-webkit-scrollbar {
  width: 0px;
  background: transparent;
}

.question-panel::-webkit-scrollbar {
  width: 0px;
  background: transparent;
}

/* Firefox隐藏滚动条 */
.question-content {
  scrollbar-width: none;
  -ms-overflow-style: none;
}

.question-panel {
  scrollbar-width: none;
  -ms-overflow-style: none;
}

/* 暗色模式问题面板样式 */
@media (prefers-color-scheme: dark) {
  .question-panel {
    background-color: #2d2d2d;
    border-bottom-color: #495057;
  }
  
  .question-tabs {
    border-bottom-color: #495057;
  }
  
  .tab-button {
    color: var(--iztro-color-text);
  }
  
  .tab-button.active {
    color: var(--primary-color);
  }
  
  .tab-button:hover {
    color: var(--primary-color);
  }
  
  .category-heading {
    color: var(--iztro-color-text);
  }
  
  .question-text {
    background-color: #343a40;
    color: var(--iztro-color-text);
  }
  
  .question-text:hover {
    background-color: #495057;
  }
  
  /* 移动端暗色模式问题面板样式 */
  @media (max-width: 768px) {
    .question-panel {
      background-color: transparent;
      box-shadow: none;
      border: none;
    }
    
    /* 键盘可见时的问题面板暗色模式样式 */
    .keyboard-visible .question-panel {
      box-shadow: none;
    }
  }
}
