___CSS_LOADER_EXPORT___.push([module.id, `/* 骨架屏加载组件样式 */

.skeleton-loader {
  width: 100%;
  height: 100%;
}

/* 骨架屏动画 */
.skeleton-animate .skeleton-line,
.skeleton-animate .skeleton-avatar,
.skeleton-animate .skeleton-title,
.skeleton-animate .skeleton-subtitle,
.skeleton-animate .skeleton-input-field,
.skeleton-animate .skeleton-input-button,
.skeleton-animate .skeleton-message-line {
  position: relative;
  overflow: hidden;
  background-color: rgba(0, 0, 0, 0.06);
}

.skeleton-animate .skeleton-line::after,
.skeleton-animate .skeleton-avatar::after,
.skeleton-animate .skeleton-title::after,
.skeleton-animate .skeleton-subtitle::after,
.skeleton-animate .skeleton-input-field::after,
.skeleton-animate .skeleton-input-button::after,
.skeleton-animate .skeleton-message-line::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(
    90deg,
    rgba(255, 255, 255, 0) 0%,
    rgba(255, 255, 255, 0.3) 50%,
    rgba(255, 255, 255, 0) 100%
  );
  animation: skeleton-loading 1.5s infinite;
  z-index: 1;
}

@keyframes skeleton-loading {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(100%);
  }
}

/* 消息骨架屏样式 */
.skeleton-message {
  display: flex;
  align-items: flex-start;
  padding: 12px 16px;
  margin-bottom: 12px;
}

.skeleton-avatar {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  margin-right: 12px;
  flex-shrink: 0;
}

.skeleton-content {
  flex: 1;
  min-width: 0;
}

.skeleton-line {
  height: 16px;
  border-radius: 4px;
  margin-bottom: 8px;
}

.skeleton-line:last-child {
  margin-bottom: 0;
}

.skeleton-short {
  width: 60%;
}

.skeleton-long {
  width: 90%;
}

/* 聊天骨架屏样式 */
.skeleton-chat {
  display: flex;
  flex-direction: column;
  height: 100%;
  padding: 16px;
}

.skeleton-header {
  display: flex;
  flex-direction: column;
  padding-bottom: 16px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  margin-bottom: 16px;
}

.skeleton-title {
  height: 24px;
  border-radius: 4px;
  width: 30%;
  margin-bottom: 8px;
}

.skeleton-subtitle {
  height: 16px;
  border-radius: 4px;
  width: 20%;
}

.skeleton-messages {
  flex: 1;
  overflow-y: auto;
  margin-bottom: 16px;
}

.skeleton-message-wrapper {
  display: flex;
  align-items: flex-start;
  margin-bottom: 16px;
}

.skeleton-message-avatar {
  width: 36px;
  height: 36px;
  border-radius: 50%;
  margin-right: 12px;
  flex-shrink: 0;
}

.skeleton-message-content {
  flex: 1;
  min-width: 0;
}

.skeleton-message-line {
  height: 14px;
  border-radius: 4px;
  margin-bottom: 6px;
}

.skeleton-message-line:last-child {
  margin-bottom: 0;
}

.skeleton-input {
  display: flex;
  align-items: center;
  padding: 12px;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 24px;
}

.skeleton-input-container {
  display: flex;
  align-items: center;
  padding: 8px;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 24px;
}

.skeleton-input-field {
  flex: 1;
  height: 36px;
  border-radius: 18px;
  margin-right: 8px;
}

.skeleton-input-button {
  width: 36px;
  height: 36px;
  border-radius: 50%;
  flex-shrink: 0;
}

/* 暗色模式下的骨架屏样式 */
@media (prefers-color-scheme: dark) {
  .skeleton-animate .skeleton-line,
  .skeleton-animate .skeleton-avatar,
  .skeleton-animate .skeleton-title,
  .skeleton-animate .skeleton-subtitle,
  .skeleton-animate .skeleton-input-field,
  .skeleton-animate .skeleton-input-button,
  .skeleton-animate .skeleton-message-line {
    background-color: rgba(255, 255, 255, 0.1);
  }

  .skeleton-animate .skeleton-line::after,
  .skeleton-animate .skeleton-avatar::after,
  .skeleton-animate .skeleton-title::after,
  .skeleton-animate .skeleton-subtitle::after,
  .skeleton-animate .skeleton-input-field::after,
  .skeleton-animate .skeleton-input-button::after,
  .skeleton-animate .skeleton-message-line::after {
    background: linear-gradient(
      90deg,
      rgba(255, 255, 255, 0) 0%,
      rgba(255, 255, 255, 0.2) 50%,
      rgba(255, 255, 255, 0) 100%
    );
  }

  .skeleton-header {
    border-bottom-color: rgba(255, 255, 255, 0.1);
  }

  .skeleton-input,
  .skeleton-input-container {
    border-color: rgba(255, 255, 255, 0.1);
  }
}`, "",{"version":3,"sources":["webpack://./src/styles/components/skeleton-loader.css"],"names":[],"mappings":"AAAA,cAAc;;AAEd;EACE,WAAW;EACX,YAAY;AACd;;AAEA,UAAU;AACV;;;;;;;EAOE,kBAAkB;EAClB,gBAAgB;EAChB,qCAAqC;AACvC;;AAEA;;;;;;;EAOE,WAAW;EACX,kBAAkB;EAClB,MAAM;EACN,OAAO;EACP,QAAQ;EACR,SAAS;EACT;;;;;GAKC;EACD,yCAAyC;EACzC,UAAU;AACZ;;AAEA;EACE;IACE,4BAA4B;EAC9B;EACA;IACE,2BAA2B;EAC7B;AACF;;AAEA,YAAY;AACZ;EACE,aAAa;EACb,uBAAuB;EACvB,kBAAkB;EAClB,mBAAmB;AACrB;;AAEA;EACE,WAAW;EACX,YAAY;EACZ,kBAAkB;EAClB,kBAAkB;EAClB,cAAc;AAChB;;AAEA;EACE,OAAO;EACP,YAAY;AACd;;AAEA;EACE,YAAY;EACZ,kBAAkB;EAClB,kBAAkB;AACpB;;AAEA;EACE,gBAAgB;AAClB;;AAEA;EACE,UAAU;AACZ;;AAEA;EACE,UAAU;AACZ;;AAEA,YAAY;AACZ;EACE,aAAa;EACb,sBAAsB;EACtB,YAAY;EACZ,aAAa;AACf;;AAEA;EACE,aAAa;EACb,sBAAsB;EACtB,oBAAoB;EACpB,2CAA2C;EAC3C,mBAAmB;AACrB;;AAEA;EACE,YAAY;EACZ,kBAAkB;EAClB,UAAU;EACV,kBAAkB;AACpB;;AAEA;EACE,YAAY;EACZ,kBAAkB;EAClB,UAAU;AACZ;;AAEA;EACE,OAAO;EACP,gBAAgB;EAChB,mBAAmB;AACrB;;AAEA;EACE,aAAa;EACb,uBAAuB;EACvB,mBAAmB;AACrB;;AAEA;EACE,WAAW;EACX,YAAY;EACZ,kBAAkB;EAClB,kBAAkB;EAClB,cAAc;AAChB;;AAEA;EACE,OAAO;EACP,YAAY;AACd;;AAEA;EACE,YAAY;EACZ,kBAAkB;EAClB,kBAAkB;AACpB;;AAEA;EACE,gBAAgB;AAClB;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,aAAa;EACb,oCAAoC;EACpC,mBAAmB;AACrB;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,YAAY;EACZ,oCAAoC;EACpC,mBAAmB;AACrB;;AAEA;EACE,OAAO;EACP,YAAY;EACZ,mBAAmB;EACnB,iBAAiB;AACnB;;AAEA;EACE,WAAW;EACX,YAAY;EACZ,kBAAkB;EAClB,cAAc;AAChB;;AAEA,gBAAgB;AAChB;EACE;;;;;;;IAOE,0CAA0C;EAC5C;;EAEA;;;;;;;IAOE;;;;;KAKC;EACH;;EAEA;IACE,6CAA6C;EAC/C;;EAEA;;IAEE,sCAAsC;EACxC;AACF","sourcesContent":["/* 骨架屏加载组件样式 */\n\n.skeleton-loader {\n  width: 100%;\n  height: 100%;\n}\n\n/* 骨架屏动画 */\n.skeleton-animate .skeleton-line,\n.skeleton-animate .skeleton-avatar,\n.skeleton-animate .skeleton-title,\n.skeleton-animate .skeleton-subtitle,\n.skeleton-animate .skeleton-input-field,\n.skeleton-animate .skeleton-input-button,\n.skeleton-animate .skeleton-message-line {\n  position: relative;\n  overflow: hidden;\n  backgro